/**
 * Events
 */

.tribe_events {
  .entry-hero {
    position: relative;

    .event-caption {
      position: absolute;

      top: 0.25em;
      left: 0.5em;

      max-width: 25%;

      color: color(white);

      .event-date,
      .event-month {
        display: block;
      }
    }
  }

  &.excerpted-post {
    .entry-excerpt,
    &:after {
      display: none;
    }

    .entry-footer {
      flex: 1 0 auto;
      z-index: 2;
      display: block;
      max-height: 30px;

      box-shadow: $box-shadow;

      @at-root .widget.most_featured_event & {
        flex: 1 0 100%;
        order: 0;

        display: block;
      }
    }
  }
}

#tribe-events-pg-template {
  max-width: 100%;
}

#tribe-bar-form {
  backround-color: color(grey);

  @include media( $third-break ) {
    display: flex;
    flex-wrap: nowrap;
  }

  label {
  }

  input[type=text] {
  }

  .tribe-bar-date-filter,
  .tribe-bar-search-filter,
  .tribe-bar-submit {
    padding-top: 0px;
    padding-bottom: 0px;
  }

  .tribe-bar-submit input[type=submit] {
    background-color: color(red);
  }

  #tribe-bar-views {
    @include media( $third-break ) {
      flex: 0 1 flex-grid(3, 12);
      order: 2;

      width: auto;
      left: auto;
    }

    .tribe-bar-views-inner {
      padding: 0.75em 0;

      background: color(red);
      color: color(white);

      > * {
      }

      label {
        display: inline-block;
        //float: left;
      }

      .tribe-bar-views-list {
        //display: inline-block;
        top: 0.75em;
        right: 1em;
        left: auto;
        width: auto;
        min-width: 111px;

        float: none;
      }
    }

    & + .tribe-bar-filters {
      width: auto;
      right: auto;

      @include media( $third-break ) {
        flex: 1 1 flex-grid(9, 12);
      }
    }
  }
}

#tribe_events_filters_wrapper {
  .tribe-filters-open & {
    height: auto;
  }

  #tribe_events_filter_control {
    margin-bottom: 0;

    font-weight: 300;
    font-style: italic;
    text-transform: uppercase;
  }

  .tribe-events-filters-content {
    background-color: transparent;

    border-right: 0;
    border-left: 0;

    .tribe-events-filters-label {
      font-weight: 300;
      font-style: italic;
      text-transform: uppercase;
    }
  }

  .tribe_events_filter_item {
    margin-bottom: 2em;

    .tribe-events-filters-group-heading {
      padding: 0.33em 1em 0.33em 1.5em;

      font-size: em(12, 12);
      font-style: italic;
      text-transform: uppercase;

      &:before {
        margin-left: -1em;

        border-top-width: 10px;
        border-left-width: 5px;
        border-right-width: 5px;
      }
    }

    .tribe-events-filter-group {
      border-bottom: 0;

      li {
        background-color: transparent;
      }

      label {
        padding-top: 0.33em;
        padding-bottom: 0.33em;

        font-size: em(16, 12);

        border-bottom: 0;

        input[type=checkbox] {
        }

        span {
        }
      }
    }

    &.closed {
      .tribe-events-filters-group-heading {
        &:before {
          border-bottom-width: 5px;
          border-left-width: 10px;
          border-top-width: 5px;
        }
      }
    }
  }
}

#tribe_events_filters_reset {
  font-weight: 300;
  font-style: italic;
  text-transform: uppercase;
  text-decoration: none;
}

.tribe-events-page-title {
}

#tribe-events-header {
  .tribe-events-sub-nav {
  }
}

.tribe-events-list-separator-month {
  @extend %h5;

  margin: 1em 0;
  padding: 5px 10px;

  background-color: color(red);
  color: color(white);

  font-size: em(16, 14);
  text-align: left;

  span {
  }
}

.tribe-events-list .tribe-events-loop {
  .type-tribe_events {
    .tribe-events-event-image {
      margin-left: 0;
    }
  }
}

.tribe-events-list h2.tribe-events-page-title {
  @extend %h4;

  margin-bottom: -1.25em;

  font-size: em(24);
  font-style: normal;
}

.tribe-events-sub-nav {
  span {
    display: none;
  }

  .tribe-events-nav-left,
  .tribe-events-nav-right {
    a {
      display: block;
      @include pad(0.5rem 1rem);

      background-color: color(light-grey);
      color: color(black);

      font-size: em(14, 18);
      font-style: italic;
      text-transform: uppercase;

      transition: all 0.25s ease-out;

      &:before,
      &:after {
        color: color(red);

        font-style: normal;

        transform: translateX(0);
      }

      &:hover,
      &:focus {
        background-color: color(grey);
        color: color(light-grey);
      }

      span {
        display: none;
      }
    }
  }

  .tribe-events-nav-left {
    a {
      &:before {
        content: "←";
      }

      &:hover:before,
      &:focus:before {
        transform: translateX(-1em);
      }
    }
  }
  .tribe-events-nav-right {
    a {
      &:after {
        content: "→";
      }

      &:hover:after,
      &:focus:after {
        transform: translateX(1em);
      }
    }
  }
}

.tribe-events-list .tribe-events-loop {
  .tribe-events-list-event-title {
    color: color(black);

    a {
      color: inherit;
    }
  }

  .type-tribe_events:not(.tribe-event-featured) {
    @extend .excerpted-post;
    @extend .excerpted-post-list;

    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: flex-start;
    align-items: stretch;

    box-sizing: content-box;
    margin-bottom: 1em;
    padding-top: 0;
    padding-bottom: 1em;

    border-top: 0;

    @include media( $third-break ) {
      //max-height: 150px;
    }

    > * {
      flex: 0 1 auto;
      order: 2;
    }

    .tribe-events-event-image {
      @extend .entry-hero;

      flex: 0 0 150px;
      order: 1;

      max-width: 150px;

      .wp-post-image {
        max-height: 150px;
        max-width: 150px;
      }
    }

    .tribe-events-content {
    }
  }

  .tribe-event-featured {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;

    padding-left: 0;
    padding-right: 0;

    background-color: color(light-grey);
    color: color(black);

    &:after {
      display: none;
    }

    > * {
      flex: 1 1 100%;
    }

    a {
      color: color(red);
    }

    .tribe-events-event-image {
      order: 0;

      max-width: 100%;
    }

    .tribe-events-list-event-title,
    .tribe-events-event-meta,
    .tribe-events-content {
      order: 1;

      padding-left: flex-gutter() !important;
      padding-right: flex-gutter() !important;

      color: color(black);
    }

    .tribe-events-list-event-title {
      a {
        color: inherit;
      }
    }
  }
}
